<template>
  <nav dir="rtl" v-if="navbarcheck" class="navbar">
    <div class="navbar-brand">
      <router-link class="navbar-item" exact to="/home"
        >Black Light</router-link
      >
      <a
        :class="{
          'arrow-up': isArrowUp,
          'arrow-down': !isArrowUp,
          'navbar-button': true,
        }"
        @click="toggleNavbar"
      ></a>
    </div>

    <div v-if="isNavbarActive" class="navbar-menu">
      <router-link class="navbar-item" to="/affairs"></router-link>
      <router-link class="navbar-item" to="/PS"></router-link>
      <router-link class="navbar-item" to="/purchases"></router-link>
      <router-link class="navbar-item" to="/sales"></router-link>
      <router-link class="navbar-item" to="/harmony"></router-link>
      <router-link class="navbar-item" to="/products"></router-link>
      <router-link class="navbar-item" to="/rooms"></router-link>
      <router-link class="navbar-item" to="/expenses"></router-link>
      <router-link class="navbar-item" to="/store"></router-link>
    </div>
  </nav>

  <router-view />
</template>

<script>
import Cookies from "js-cookie";

export default {
  data() {
    return {
      navbarcheck: "",
      screenWidth: window.innerWidth,
      isNavbarActive: true,
      isArrowUp: true,
    };
  },
  created() {
    window.addEventListener("resize", this.handleResize);
    this.$router.beforeEach((to, from, next) => {
      if (to.path === "/") {
        this.navbarcheck = false;
      } else {
        this.navbarcheck = true;
      }
      next();
    });
  },
  unmounted() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      const newWidth = window.innerWidth;
      if (newWidth !== this.screenWidth) {
        this.screenWidth = newWidth;
        if (this.screenWidth > 600) {
          this.isNavbarActive = true;
        } else {
          this.isNavbarActive = false;
          this.isArrowUp = false;
        }
      }
    },
    toggleNavbar() {
      this.isNavbarActive = !this.isNavbarActive;
      this.isArrowUp = !this.isArrowUp;
    },
    getCookie(name) {
      return Cookies.get(name);
    },
  },
};
</script>

