<template>
  <div id="LoginBody">
    <form @submit.prevent="login">
      <h1>Black Light</h1>
      <input
        v-model="username"
        type="text"
        placeholder="  "
        required
      />
      <input
        type="password"
        v-model="password"
        placeholder="  "
        required
      />
      <button type="submit"> </button>
      <p id="login_error" v-if="errorMessage">{{ errorMessage }}</p>
    </form>
  </div>
</template>

<style lang="scss">
#LoginBody {
  width: 100%;
  height: 100vh;
  position: relative;
  top: 0%;
  left: 0%;
  form {
    position: absolute;
    width: 300px;
    height: 300px;
    background-color: rgba(144, 81, 0, 0.745);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    border-radius: 10px;
    box-shadow: 4px 4px 4px rgba(255, 255, 255, 0.314);
    transition: all 0.4s;
    h1 {
      font-weight: bold;
      font-size: 36px;
    }
    input {
      direction: rtl;
      width: 250px;
      height: 40px;
      text-indent: 20px;
      border-radius: 20px;
      border: none;
      outline: none;
      cursor: pointer;
      font-size: 16px;
    }
    button {
      width: 190px;
      height: 38px;
      border-radius: 10px;
      border: none;
      outline: none;
      background-color: rgb(120, 38, 0);
      color: white;
      font-size: 16px;
      font-weight: bold;
      cursor: pointer;
    }
    #login_error {
      direction: rtl;
      font-size: 13px;
    }
  }
}
</style>

<script>
import axios from "axios";
import Cookies from "js-cookie";

export default {
  data() {
    return {
      username: "",
      password: "",
      errorMessage: "",
    };
  },
  created() {
    if (this.getCookie("user_id")) {
      this.$router.push("/home");
    }
  },
  methods: {
    async login() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/login.php",
          {
            username: this.username,
            password: this.password,
          }
        );
        if (response.status === 200) {
          const message = response.data.message;
          if (message == true) {
            this.$router.push("/home");
            this.setSecureCookie("user_id", response.data.user.id, 7);
            this.setSecureCookie("user_name", response.data.user.username, 7);
            this.setSecureCookie("user_role", response.data.user.role, 7);
            this.setSecureCookie("user_affairs", response.data.user.affairs, 7);
            this.setSecureCookie("user_ps", response.data.user.ps, 7);
            this.setSecureCookie(
              "user_purchases",
              response.data.user.purchases,
              7
            );
            this.setSecureCookie("user_sales", response.data.user.sales, 7);
            this.setSecureCookie("user_harmony", response.data.user.harmony, 7);
            this.setSecureCookie(
              "user_products",
              response.data.user.products,
              7
            );
            this.setSecureCookie("user_rooms", response.data.user.rooms, 7);
            this.setSecureCookie(
              "user_expenses",
              response.data.user.expenses,
              7
            );
            this.setSecureCookie("user_store", response.data.user.store, 7);
          } else {
            this.errorMessage = "      ";
            console.log(response.data);
          }
        } else {
          this.errorMessage = response.data.message;
        }
      } catch (error) {
        console.error("Login failed:", error);
        this.errorMessage = "      ";
      }
    },
    setSecureCookie(name, value, days) {
      Cookies.set(name, value, {
        expires: days,
        secure: true,
        sameSite: "Strict",
      });
    },
    getCookie(name) {
      return Cookies.get(name);
    },
  },
};
</script>
