<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      // Show Page
      RoomsShow: "",
      id: "",
      username: "",
      // Rooms
      Places: [
        { id: "Room1", Room: " 1", items: [], Total: 0 },
        { id: "Room2", Room: " 2", items: [], Total: 0 },
        { id: "Room3", Room: " 3", items: [], Total: 0 },
        { id: "Room4", Room: " 4", items: [], Total: 0 },
        { id: "Room5", Room: " 5", items: [], Total: 0 },
        { id: "Room6", Room: " 6", items: [], Total: 0 },
        { id: "Room7", Room: " 7", items: [], Total: 0 },
        { id: "Room8", Room: " 8", items: [], Total: 0 },
        { id: "PS1", Room: "PS1", items: [], Total: 0 },
        { id: "PS2", Room: "PS2", items: [], Total: 0 },
        { id: "PS3", Room: "PS3", items: [], Total: 0 },
        { id: "PS4", Room: "PS4", items: [], Total: 0 },
        { id: "PS5", Room: "PS5", items: [], Total: 0 },
        { id: "PS6", Room: "PS6", items: [], Total: 0 },
        { id: "PS7", Room: "PS7", items: [], Total: 0 },
        { id: "PS8", Room: "PS8", items: [], Total: 0 },
        { id: "Delivery", Room: "Delivery", items: [], Total: 0 },
        { id: "TakeAway", Room: "TakeAway", items: [], Total: 0 },
      ],
      NewItem: "",
      NewQuantity: "",
      NewPlace: "",
      Products: [],
      OPlace: "",
      NPlace: "",
      //PSs
      PSs: [],
      PSW: ["", "", "", "", "", "", "", ""],
      PST: [0, 0, 0, 0, 0, 0, 0, 0],
      PSWT: "",
      PSTT: "",
      // time
      time: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.username = this.getCookie("user_name");
    this.RoomsShow = this.getCookie("user_rooms");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
    this.GetProducts();
    this.getTime();
  },
  mounted() {
    this.startInterval();
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async GetProducts() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/orders.php"
        );
        this.Products = response.data.Products;
        this.Places = JSON.parse(response.data.Places[0].orders);
        this.PSW = JSON.parse(response.data.Places[1].orders);
        this.PST = JSON.parse(response.data.Places[2].orders);
        this.PSs = response.data.PSs;
        this.$forceUpdate();
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async AddItem() {
      const price = Number(
        this.Products.filter((Product) => Product.id == this.NewItem)[0].price
      ).toFixed(2);
      const name = this.Products.filter(
        (Product) => Product.id == this.NewItem
      )[0].name;
      this.Places[this.NewPlace].items.push({
        Order: name,
        Quantity: Math.abs(this.NewQuantity.toFixed(2)),
        Price: price,
      });
      let total = 0;
      this.Places[this.NewPlace].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      this.Places[this.NewPlace].Total = total.toFixed(2);
      this.NewItem = "";
      this.NewQuantity = "";
      this.NewPlace = "";
    },
    RemoveItem(index, placeId, key) {
      const order = this.Places[index].items[key].Order;
      if (
        !(
          order == "normal" ||
          order == "multi" ||
          order == "plus" ||
          order == "netflix"
        )
      ) {
        this.Places.forEach((place) => {
          if (place.id === placeId) {
            place.items.splice(key, 1);
            let total = 0;
            place.items.forEach((item) => {
              total += item.Quantity * item.Price;
            });
            place.Total = total.toFixed(2);
          }
        });
        this.UpdateItems();
      } else {
        alert("  ");
      }
    },
    async UpdateItems() {
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            JSON: this.Places,
            JSONW: this.PSW,
            JSONT: this.PST,
          }
        );
        this.$forceUpdate();
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
    },
    async PlayPS(index) {
      if (this.PSWT == "") {
        alert("  ");
      } else {
        this.getTime();
        try {
          const response = await axios.put(
            "http://localhost/cafe-ps8/api/orders.php",
            {
              IdPS: index + 1,
              Time: this.time,
            }
          );
          console.log(response.data.msg);
        } catch (error) {
          console.error("fail:", error);
        }
        if (this.PSTT == 0 || this.PSTT == "") {
          this.PST[index] = 0;
        } else {
          this.PST[index] = this.PSTT;
        }
        this.PSW[index] = this.PSWT;
        this.PSWT = "";
        this.PSTT = "";
        this.UpdateItems();
        this.GetProducts();
      }
    },
    async StopPS(index) {
      this.getTime();
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            IdPS: index + 1,
            stop: 0,
          }
        );
        console.log(response.data.msg);
      } catch (error) {
        console.error("fail:", error);
      }
      let price;
      if (this.PSW[index] == "normal") {
        price = this.PSs[index].normal_price;
      } else if (this.PSW[index] == "multi") {
        price = this.PSs[index].multi_price;
      } else if (this.PSW[index] == "plus") {
        price = this.PSs[index].plus_price;
      } else if (this.PSW[index] == "netflix") {
        price = this.PSs[index].netflix_price;
      }
      if (this.PST[index] == 0) {
        const Start = new Date(this.PSs[index].start.replace(" ", "T"));
        const End = new Date(this.time.replace(" ", "T"));
        const hours = (
          (Math.floor((End - Start) / (1000 * 60)) % 60) /
          60
        ).toFixed(2);
        this.PST[index] = hours;
      }
      this.Places[index + 8].items.unshift({
        Order: this.PSW[index],
        Quantity: this.PST[index],
        Price: price,
      });
      let total = 0;
      this.Places[index + 8].items.forEach((item) => {
        total += item.Quantity * item.Price;
      });
      this.Places[index + 8].Total = total.toFixed(2);
      this.PSW[index] = "";
      this.PST[index] = 0;
      await this.UpdateItems();
      this.GetProducts();
    },
    startInterval() {
      setInterval(() => {
        this.getTime();
        for (let i = 0; i < 8; i++) {
          if (this.PST[i] != 0 && this.PSs[i].state == 1) {
            const Start = new Date(this.PSs[i].start.replace(" ", "T"));
            const End = new Date(this.time.replace(" ", "T"));
            const hours = (
              (Math.floor((End - Start) / (1000 * 60)) % 60) /
              60
            ).toFixed(2);
            console.log(hours);
            if (this.PST[i] - hours <= 0) {
              this.StopPS(i);
            }
          }
        }
      }, 60000);
    },
    async ChangePlaces() {
      const place = this.Places[this.OPlace].items;
      this.Places[this.OPlace].items = this.Places[this.NPlace].items;
      this.Places[this.NPlace].items = place;
      const Total = this.Places[this.OPlace].Total;
      this.Places[this.OPlace].Total = this.Places[this.NPlace].Total;
      this.Places[this.NPlace].Total = Total;
      await this.UpdateItems();
      await this.GetProducts();
      this.NPlace = "";
      this.OPlace = "";
      this.$router.go();
    },
    async Print(index) {
      if (this.Places[index].Total != 0) {
        const printContents = this.$refs["printsection" + index][0].outerHTML;
        const originalContents = this.$refs.room.innerHTML;
        this.$refs.room.innerHTML = printContents;
        await this.$nextTick();
        window.print();
        await this.checkdb(index);
        this.$refs.room.innerHTML = originalContents;
        await window.location.reload();
      } else {
        alert(" ");
      }
    },
    async checkdb(place) {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/orders.php",
          {
            Id: this.id,
            Time: this.time,
            Orders: this.Places[place].items,
            Place: place,
          }
        );
        console.log(response.data);
        if (response.data.message == " ") {
          this.Places[place].items = [];
          this.Places[place].Total = 0;
        }
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      await this.UpdateItems();
      this.GetProducts();
    },
    getTime() {
      const now = new Date();

      // Get date components
      const year = now.getFullYear();
      const month = String(now.getMonth() + 1).padStart(2, "0"); // Month is zero-based
      const day = String(now.getDate()).padStart(2, "0");

      // Get time components
      const hours = String(now.getHours()).padStart(2, "0");
      const minutes = String(now.getMinutes()).padStart(2, "0");
      const seconds = String(now.getSeconds()).padStart(2, "0");

      // Create formatted datetime string
      this.time = `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
    },
  },
};
</script>
