<template>
  <div v-if="ShowProductsPage == 1" id="ProductsBody">
    <div class="field">
      <select @change="GetSectors" required v-model="SelectCafeRest">
        <option disabled value=""> / </option>
        <option value="1"></option>
        <option value="0"></option>
      </select>
    </div>
    <div v-if="SectorShow" class="field">
      <select @change="GetProducts" required v-model="Sector">
        <option disabled value=""></option>
        <option v-for="(sec, index) in Sectors" :key="index" :value="sec.id">
          {{ sec.name }}
        </option>
      </select>
      <form @submit.prevent="AddSector">
        <input
          v-model="NewSector"
          type="text"
          placeholder="  "
          required
        />
        <button type="submit"></button>
      </form>
      <form @submit.prevent="DeleteSector">
        <select required v-model="OldSector">
          <option disabled value=""></option>
          <option
            v-for="(Sector, index) in Sectors"
            :key="index"
            :value="Sector.id"
          >
            {{ Sector.name }}
          </option>
        </select>
        <button type="submit"></button>
      </form>
    </div>
    <div v-if="ProductsShow == 1" class="field">
      <h1></h1>
      <div>
        <table dir="rtl" id="ProductsTable">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th> </th>
            <th> </th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(product, index) in Products" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ product.name }}</td>
            <td>{{ product.price }}</td>
            <td>{{ product.num_sales }}</td>
            <td v-if="product.comp == 1"> </td>
            <td v-else>  </td>
            <td @click="EditProduct(product)"></td>
            <td @click="DeleteProduct(product)"></td>
          </tr>
        </table>
      </div>
      <form @submit.prevent="AddEditProduct">
        <input v-model="ProductName" type="text" placeholder="" required />
        <input
          v-model="ProductPrice"
          type="number"
          placeholder=""
          required
        />
        <select required v-model="ProductComp">
          <option disabled value="">  </option>
          <option value="1"></option>
          <option value="0"></option>
        </select>
        <button type="submit">{{ ProductAddEdit }}</button>
      </form>
    </div>
    <div v-if="MaterialsShow == 1" class="field">
      <h1> </h1>
      <div>
        <table dir="rtl" id="MaterialsTable">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th> </th>
            <th> </th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(material, index) in Materials" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ material.name }}</td>
            <td>{{ material.price }}</td>
            <td>{{ material.num_store }}</td>
            <td>{{ material.num_purchases }}</td>
            <td @click="EditMaterial(material)"></td>
            <td @click="DeleteMaterial(material)"></td>
          </tr>
        </table>
      </div>
      <form @submit.prevent="AddEditMaterial">
        <input
          v-model="MaterialName"
          type="text"
          placeholder=""
          required
        />
        <input
          v-model="MaterialPrice"
          type="number"
          placeholder=""
          required
        />
        <button type="submit">{{ MaterialAddEdit }}</button>
      </form>
    </div>
    <hr />
    <div v-if="ProductsShow == 1" class="field">
      <select required v-model="ProductsMaterials">
        <option disabled value="">  </option>
        <option
          v-for="(Product, index) in Products"
          :key="index"
          :value="Product.id"
        >
          {{ Product.name }}
        </option>
      </select>
      <form @submit.prevent="AddPM">
        <select required v-model="PMid">
          <option disabled value="">  </option>
          <option
            v-for="(material, index) in AllMaterials"
            :key="index"
            :value="material.id"
          >
            {{ material.name }}
          </option>
        </select>
        <input v-model="PMnum" type="text" placeholder="" required />
        <button type="submit"></button>
      </form>
    </div>
    <div v-if="ProductsShow == 1" class="field">
      <div>
        <table dir="rtl" id="PMTable">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr v-for="(pm, index) in ProductsMaterialsArr" :key="index">
            <td>{{ index + 1 }}</td>
            <td>{{ pm.num }}</td>
            <td>{{ pm.name }}</td>
            <td @click="DeletePM(pm)"></td>
          </tr>
        </table>
      </div>
    </div>
  </div>
  <div class="no" v-else>
    <div> ,     </div>
  </div>
</template>

<style lang="scss">
