  width: 100%;
  font-size: 22px;
  font-weight: bold;
  h1 {
    background-color: rgba(255, 201, 8, 0.537);
    padding: 2px;
    margin: 2px auto;
    width: 50%;
    height: 30px;
    border-radius: 15px;
    font-size: 20px;
    text-align: center;
  }
  hr {
    width: 100%;
    color: white;
    background-color: white;
    height: 5px;
  }
  .field {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    padding: 8px;
    div {
      width: 100%;
      overflow: auto;
      table {
        width: 100%;
        margin-top: 20px;
        text-align: center;
        th {
          background-color: black;
          color: white;
          padding: 4px 1px;
        }
        tr:nth-child(even) td {
          background-color: rgba(255, 201, 8, 0.537);
          padding: 4px 1px;
        }
        tr:nth-child(odd) td {
          background-color: rgba(255, 235, 133, 0.614);
          padding: 4px 1px;
        }
        tr:hover td {
          background-color: rgb(177, 185, 59);
        }
      }
    }
    form {
      display: flex;
      justify-content: space-around;
      align-items: center;
      flex-wrap: wrap;
    }
  }
  input,
  select {
    width: 200px;
    height: 30px;
    border: none;
    outline: none;
    border-radius: 10px;
    text-indent: 20px;
