    margin: 5px;
  }
  button {
    width: 220px;
    height: 30px;
    margin: 5px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: rgb(154, 116, 0);
    color: white;
    font-size: 14px;
    font-weight: bold;
    cursor: pointer;
  }
}
.no {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: calc(100vh - 60px);
  div {
    font-size: 26px;
    font-weight: bold;
    color: black;
    background-color: rgba(144, 81, 0, 0.9);
    width: 80%;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 15px;
    @media (max-width: 900px) {
      font-size: 22px;
      width: 90%;
    }
    @media (max-width: 650px) {
      font-size: 16px;
      width: 95%;
    }
    @media (max-width: 500px) {
