<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      // User Info
      id: "",
      // Show Page
      ShowProductsPage: "",
      // Cafe / Restaurant Selection
      SelectCafeRest: "",
      // Sector / Selection
      SectorShow: 0,
      Sector: "",
      Sectors: [],
      NewSector: "",
      OldSector: "",
      // Products
      Products: [],
      ProductsShow: 0,
      ProductId: "",
      ProductName: "",
      ProductPrice: "",
      ProductComp: "",
      ProductAddEdit: "",
      // Materials
      Materials: [],
      AllMaterials: [],
      MaterialsShow: 0,
      MaterialId: "",
      MaterialName: "",
      MaterialPrice: "",
      MaterialAddEdit: "",
      // Products Materials
      ProductsMaterials: "",
      MaterialsProducts: "",
      ProductsMaterialsArr: [],
      PMid: "",
      PMnum: "",
    };
  },
  watch: {
    SelectCafeRest: function (v) {
      if (v == "") {
        this.SectorShow = 0;
        this.Sector = "";
        this.Sectors = [];
        this.Products = [];
        this.ProductsShow = 0;
        this.Materials = [];
        this.MaterialsShow = 0;
      }
    },
    Sector: function (v) {
      if (v == "") {
        this.Products = [];
        this.ProductsShow = 0;
        this.Materials = [];
        this.MaterialsShow = 0;
      }
    },
    ProductsMaterials: function (v) {
      if (v != "") {
        this.GetProductsMaterials();
      }
    },
  },
  created() {
    this.id = this.getCookie("user_id");
    this.ShowProductsPage = this.getCookie("user_products");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async GetSectors() {
      if (this.SelectCafeRest != "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "Sectors",
              SelectCafeRest: this.SelectCafeRest,
            }
          );
          this.Sectors = response.data.Sectors;
          this.AllMaterials = response.data.AllMaterials;
          this.SectorShow = 1;
          this.Sector = "";
        } catch (error) {
          console.error("fail:", error);
        }
      }
      this.Products = [];
      this.ProductsShow = 0;
      this.Materials = [];
      this.MaterialsShow = 0;
    },
    async AddSector() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/products.php",
          {
            Type: "AddSector",
            SelectCafeRest: this.SelectCafeRest,
            NewSector: this.NewSector,
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      this.NewSector = "";
      this.GetSectors();
    },
    async DeleteSector() {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/products.php",
          {
            data: {
              Type: "Sector",
              OldSector: this.OldSector,
            },
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
      this.OldSector = "";
      this.Sector = "";
      this.GetSectors();
    },
    async GetProducts() {
      if (this.Sector != "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "Products",
              Sector: this.Sector,
            }
          );
          this.Products = response.data.Products;
          this.ProductsShow = 1;
          this.Materials = response.data.Materials;
          this.MaterialsShow = 1;
        } catch (error) {
          console.error("fail:", error);
        }
      }
    },
    EditProduct(product) {
      this.ProductId = product.id;
      this.ProductName = product.name;
      this.ProductPrice = product.price;
      this.ProductComp = product.comp;
      this.ProductAddEdit = "";
    },
    async AddEditProduct() {
      if (this.ProductAddEdit == "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "AddProduct",
              ProductName: this.ProductName,
              ProductPrice: Math.abs(this.ProductPrice),
              ProductComp: this.ProductComp,
              ProductSector: this.Sector,
            }
          );
          alert(response.data.message);
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      } else {
        try {
          const response = await axios.put(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "product",
              ProductId: this.ProductId,
              ProductName: this.ProductName,
              ProductPrice: Math.abs(this.ProductPrice),
              ProductComp: this.ProductComp,
            }
          );
          alert(response.data.message);
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      }
      this.ProductName = "";
      this.ProductPrice = "";
      this.ProductComp = "";
      this.ProductAddEdit = "";
      this.GetProducts();
    },
    async DeleteProduct(Product) {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/products.php",
          {
            data: {
              Product: Product.id,
              Type: "Product",
            },
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
      this.GetProducts();
    },
    async AddEditMaterial() {
      if (this.MaterialAddEdit == "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "AddMaterial",
              MaterialName: this.MaterialName,
              MaterialPrice: Math.abs(this.MaterialPrice),
              MaterialSector: this.Sector,
            }
          );
          alert(response.data.message);
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      } else {
        try {
          const response = await axios.put(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "Material",
              MaterialId: this.MaterialId,
              MaterialName: this.MaterialName,
              MaterialPrice: Math.abs(this.MaterialPrice),
            }
          );
          alert(response.data.message);
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      }
      this.MaterialName = "";
      this.MaterialPrice = "";
      this.GetProducts();
      this.GetSectors();
      this.MaterialAddEdit = "";
    },
    EditMaterial(Material) {
      this.MaterialId = Material.id;
      this.MaterialName = Material.name;
      this.MaterialPrice = Material.price;
      this.MaterialAddEdit = "";
    },
    async DeleteMaterial(Material) {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/products.php",
          {
            data: {
              Material: Material.id,
              Type: "Material",
            },
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
      this.GetProducts();
    },
    async GetProductsMaterials() {
      if (this.ProductsMaterials != "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/products.php",
            {
              Type: "ProductsMaterials",
              ProductsMaterials: this.ProductsMaterials,
            }
          );
          this.ProductsMaterialsArr = response.data.ProductsMaterialsArr;
        } catch (error) {
          console.error("fail:", error);
        }
      }
    },
    async AddPM() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/products.php",
          {
            Type: "AddPM",
            PMid: this.PMid,
            PMnum: Math.abs(this.PMnum),
            PMproduct: this.ProductsMaterials,
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      this.PMid = "";
      this.PMnum = "";
      this.GetProductsMaterials();
    },
    async DeletePM(ProductsMaterials) {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/products.php",
          {
            data: {
              ProductsMaterials: ProductsMaterials.id,
              Type: "ProductsMaterials",
            },
          }
        );
        alert(response.data.message);
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
      this.GetProductsMaterials();
    },
  },
};
</script>
