<template>
  <div v-if="ShowPurchases == 1" id="PurchasesBody">
    <!-- Select Cafe / Restaurant -->
    <select v-model="SelectKindModel">
      <option disabled value=""> \ </option>
      <option value="1"></option>
      <option value="0"></option>
    </select>
    <!-- Select Sector -->
    <select v-if="SectorShow == 1" v-model="SelectSector">
      <option disabled value=""> </option>
      <option v-for="(item, index) in Sectors" :key="index" :value="item.id">
        {{ item.name }}
      </option>
    </select>
    <div>
      <table v-if="MaterialShow == 1">
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(Material, index) in Materials" :key="index">
          <td>{{ index + 1 }}</td>
          <td>{{ Material.name }}</td>
          <td>{{ Material.num_purchases }}</td>
          <td class="point" @click="MaterialIncrement(Material)">+</td>
          <td class="point" @click="MaterialDecreasement(Material)">-</td>
        </tr>
      </table>
    </div>
  </div>
  <div class="no" v-else>
    <div> ,     </div>
  </div>
</template>

<style lang="scss">
#PurchasesBody {
  width: 100%;
  min-height: calc(100vh - 60px);
  select {
    display: block;
    width: 50%;
    height: 50px;
    border-radius: 25px;
    border: none;
    outline: none;
    text-indent: 25px;
    direction: rtl;
    font-size: 18px;
    margin: 10px auto;
  }
  div {
    width: 100%;
    overflow: auto;
    table {
      direction: rtl;
      width: 100%;
      margin-top: 20px;
      text-align: center;
      .point {
        cursor: pointer;
      }
      th {
        background-color: black;
        color: white;
        padding: 4px 1px;
      }
      tr:nth-child(even) td {
        background-color: rgba(255, 201, 8, 0.537);
        padding: 4px 1px;
      }
      tr:nth-child(odd) td {
        background-color: rgba(255, 235, 133, 0.614);
        padding: 4px 1px;
      }
      tr:hover td {
        background-color: rgb(177, 185, 59);
      }
    }
  }
}
.no {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: calc(100vh - 60px);
  div {
    font-size: 24px;
    font-weight: bold;
    color: black;
    background-color: rgba(144, 81, 0, 0.9);
    width: 80%;
    height: 60px;
    line-height: 60px;
    text-align: center;
    border-radius: 15px;
    @media (max-width: 900px) {
      font-size: 20px;
      width: 90%;
    }
    @media (max-width: 650px) {
      font-size: 16px;
      width: 95%;
    }
    @media (max-width: 500px) {
      font-size: 12px;
    }
    @media (max-width: 400px) {
      font-size: 10px;
    }
  }
  @media (max-width: 600px) {
    min-height: calc(100vh - 30px);
  }
}
</style>

<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      id: "",
      ShowPurchases: "",
      SelectKindModel: "",
      // Select Sectors
      Sectors: [],
      SelectSector: "",
      SectorShow: "",
      // Select Materials
      Materials: [],
      MaterialShow: "",
    };
  },
  watch: {
    SelectKindModel: function () {
      this.SelectSector = "";
      this.getSectors();
    },
    SelectSector: function () {
      this.getMaterials();
    },
  },
  created() {
    this.id = this.getCookie("user_id");
    this.ShowPurchases = this.getCookie("user_purchases");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async getSectors() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/purchases.php",
          {
            GetType: "Sectors",
            GetSectors: this.SelectKindModel,
          }
        );
        this.Sectors = response.data.Sectors;
      } catch (error) {
        console.error("error:", error);
      }
      if (this.SelectKindModel == "") {
        this.SectorShow = 0;
      } else {
        this.SectorShow = 1;
      }
    },
    async getMaterials() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/purchases.php",
          {
            GetType: "Materials",
            GetSector: this.SelectSector,
          }
        );
        this.Materials = response.data.Materials;
      } catch (error) {
        console.error("error:", error);
      }
      if (this.SelectSector == "") {
        this.MaterialShow = 0;
      } else {
        this.MaterialShow = 1;
      }
    },
    async MaterialIncrement(material) {
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/purchases.php",
          {
            id_user: this.id,
            Type: 1,
            id: material.id,
            num: material.num_purchases,
          }
        );
        console.log(response.data.message);
      } catch (error) {
        console.error("error:", error);
      }
      this.getMaterials();
    },
    async MaterialDecreasement(material) {
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/purchases.php",
          {
            id_user: this.id,
            Type: 0,
            id: material.id,
            num: material.num_purchases,
          }
        );
        console.log(response.data.message);
      } catch (error) {
        console.error("error:", error);
      }
      this.getMaterials();
    },
  },
};
</script>
