<template>
  <div id="HomeBody">
    <h1>Black Light</h1>
    <h2>{{ username }}</h2>
    <h3 v-if="role == 1">Admin</h3>
    <h3 v-else>User</h3>
    <button @click="logout"> </button>
  </div>
</template>

<style lang="scss">
#HomeBody {
  width: 100%;
  height: calc(100vh - 60px);
  position: relative;
  top: 0%;
  left: 0%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  font-size: 22px;
  font-weight: bold;
  @media (max-width: 600px) {
    height: calc(100vh - 30px);
  }
  h1 {
    font-size: 72px;
    color: white;
    @media (max-width: 730px) {
      font-size: 50px;
    }
  }
  h2 {
    font-size: 32px;
    color: white;
    @media (max-width: 730px) {
      font-size: 20px;
    }
  }
  h3 {
    font-size: 26px;
    color: white;
    @media (max-width: 730px) {
      font-size: 14px;
    }
  }
  button {
    width: 150px;
    height: 30px;
    margin: 20px 0px;
    padding: 5px;
    border-radius: 10px;
    border: none;
    outline: none;
    background-color: rgb(120, 38, 0);
    color: white;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
  }
}
</style>

<script>
import Cookies from "js-cookie";

export default {
  data() {
    return {
      id: "",
      username: "",
      role: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.username = this.getCookie("user_name");
    this.role = this.getCookie("user_role");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
  },

  methods: {
    logout() {
      this.removeCookie("user_id");
      this.removeCookie("user_name");
      this.removeCookie("user_role");
      this.$router.push("/");
    },
    removeCookie(name) {
      Cookies.remove(name, { secure: true, sameSite: "Strict" });
    },
    getCookie(name) {
      return Cookies.get(name);
    },
  },
};
</script>
