<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      id: "",
      SPS: "",
      PSs: [],
      ps: "",
      normal: "",
      normal_price: "",
      multi: "",
      multi_price: "",
      plus: "",
      plus_price: "",
      netflix: "",
      netflix_price: "",
      type: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.SPS = this.getCookie("user_ps");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
    this.getps();
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async getps() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/ps.php"
        );
        this.PSs = response.data.PSs;
      } catch (error) {
        console.error("Login failed:", error);
      }
    },
    EditUser(ps) {
      this.ps = ps.name;
    },
    async EditUserApi() {
      try {
        const response = await axios.put(
          "http://localhost/cafe-ps8/api/ps.php",
          {
            name: this.ps,
            normal: this.normal,
            normal_price: Math.abs(this.normal_price),
            multi: this.multi,
            multi_price: Math.abs(this.multi_price),
            plus: this.plus,
            plus_price: Math.abs(this.plus_price),
            netflix: this.netflix,
            netflix_price: Math.abs(this.netflix_price),
            type: this.type,
          }
        );
        alert(response.data.message);
        this.getps();
        this.addedit = "";
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      this.ps = "";
      this.normal = "";
      this.normal_price = "";
      this.multi = "";
      this.multi_price = "";
      this.plus = "";
      this.plus_price = "";
      this.netflix = "";
      this.netflix_price = "";
      this.type = "";
    },
  },
};
</script>
