<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      id: "",
      username: "",
      affairs: "",
      ps: "",
      purchases: "",
      sales: "",
      harmony: "",
      products: "",
      rooms: "",
      expenses: "",
      store: "",
      Saffairs: "",
      idform: "",
      password: "",
      users: [],
      addedit: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.Saffairs = this.getCookie("user_affairs");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
    this.getusers();
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async getusers() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/affairs.php"
        );
        this.users = response.data.users;
      } catch (error) {
        console.error("Login failed:", error);
      }
    },
    async DeleteUser(user) {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/affairs.php",
          {
            data: {
              id: user.id,
            },
          }
        );
        alert(response.data.message);
        this.getusers();
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
    },
    EditUser(user) {
      this.idform = user.id;
      this.username = user.username;
      this.addedit = "";
    },
    async AddEditUser() {
      if (this.addedit == "") {
        try {
          const response = await axios.post(
            "http://localhost/cafe-ps8/api/affairs.php",
            {
              username: this.username,
              password: this.password,
              affairs: this.affairs,
              ps: this.ps,
              purchases: this.purchases,
              sales: this.sales,
              harmony: this.harmony,
              products: this.products,
              rooms: this.rooms,
              expenses: this.expenses,
              store: this.store,
            }
          );
          alert(response.data.message);
          this.getusers();
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      } else {
        try {
          const response = await axios.put(
            "http://localhost/cafe-ps8/api/affairs.php",
            {
              id: this.idform,
              username: this.username,
              password: this.password,
              affairs: this.affairs,
              ps: this.ps,
              purchases: this.purchases,
              sales: this.sales,
              harmony: this.harmony,
              products: this.products,
              rooms: this.rooms,
              expenses: this.expenses,
              store: this.store,
            }
          );
          alert(response.data.message);
          this.getusers();
          this.addedit = "";
        } catch (error) {
          console.error("Error deleting user:", error.message);
        }
      }
      this.username = "";
      this.password = "";
      this.affairs = "";
      this.ps = "";
      this.purchases = "";
      this.sales = "";
      this.harmony = "";
      this.products = "";
      this.rooms = "";
      this.expenses = "";
      this.store = "";
    },
  },
};
</script>
