<template>
  <div v-if="Sharmony == 1" id="HarmonyBody">
    <div>
      <table dir="rtl" id="HarmonyTable">
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
        <tr v-for="(harmony, index) in harmonies" :key="index">
          <td>{{ index + 1 }}</td>
          <td>{{ harmony.details }}</td>
          <td @click="DeleteHarmony(harmony)"></td>
        </tr>
      </table>
    </div>

    <form @submit.prevent="AddHarmony">
      <input v-model="details" type="text" placeholder="" required />
      <button type="submit"></button>
    </form>
  </div>
  <div class="no" v-else>
    <div> ,     </div>
  </div>
</template>

