<script>
import Cookies from "js-cookie";
import axios from "axios";

export default {
  data() {
    return {
      id: "",
      user: "",
      details: "",
      Sharmony: "",
      harmonies: [],
      addedit: "",
    };
  },
  created() {
    this.id = this.getCookie("user_id");
    this.user = this.getCookie("user_name");
    this.Sharmony = this.getCookie("user_harmony");
    if (!this.getCookie("user_id")) {
      this.$router.push("/");
    }
    this.getharmony();
  },
  methods: {
    getCookie(name) {
      return Cookies.get(name);
    },
    async getharmony() {
      try {
        const response = await axios.get(
          "http://localhost/cafe-ps8/api/harmony.php"
        );
        this.harmonies = response.data.harmonies;
        this.harmonies = this.harmonies.filter(
          (item) => item.id_user == this.id
        );
      } catch (error) {
        console.error("Login failed:", error);
      }
    },
    async DeleteHarmony(harmony) {
      try {
        const response = await axios.delete(
          "http://localhost/cafe-ps8/api/harmony.php",
          {
            data: {
              id: harmony.id,
            },
          }
        );
        alert(response.data.message);
        this.getharmony();
      } catch (error) {
        console.error("Error deleting user:", error.message);
        // Handle error appropriately
      }
    },
    async AddHarmony() {
      try {
        const response = await axios.post(
          "http://localhost/cafe-ps8/api/harmony.php",
          {
            id_user: this.id,
            details: this.details,
          }
        );
        alert(response.data.message);
        this.getharmony();
      } catch (error) {
        console.error("Error deleting user:", error.message);
      }
      this.details = "";
    },
  },
};
</script>
